.skills {
  width: 100%;

  text-align: center;

  color: rgb(36, 24, 24);
  background: black;

  padding: 50px 0;
}

.skills__wrapper {
  width: 80%;
  max-width: 1440px;

  margin: 0 auto;
}

.skills__title {
  color: orange;

  margin: 0 0 50px;
}

.skills__text {
  color: orange;

  margin: 0 0 30px;
}

.skills__list {
  .reset-list;

  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px 40px;

  color: white;

  counter-reset: counter-name;
}

.skills__item {
  width: 100%;

  font-size: 22px;
  line-height: 34px;
  letter-spacing: -0.5px;
  text-align: center;

  position: relative;

  padding-top: 50px;

  &::before {
    counter-increment: counter-name;
    content: "0" counter(counter-name);

    width: 100%;
    height: 55px;

    font-size: 28px;
    line-height: 38px;
    font-weight: 700;
    letter-spacing: -1.27px;

    position: absolute;
    top: 1px;
    left: 50%;
    transform: translateX(-50%);

    color: #0ad9c6;

    padding-top: 2px;
    box-sizing: border-box;
  }
}
