@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto-bold.woff2") format("woff2"), url("../fonts/roboto-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.page {
  width: 100%;
  height: 100%;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 28px;
  padding: 0;
  margin: 0 auto;
  box-sizing: border-box;
}
.body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  color: black;
  margin: 0;
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  padding: 0;
  margin: -1px;
}
.hide {
  display: none;
}
.reset-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.preloader {
  position: fixed;
  inset: 0;
  background: #fff1e1;
  z-index: 1001;
}
.preloader__container {
  position: relative;
  top: 50%;
  left: 50%;
  width: 70px;
  height: 70px;
  margin-top: -35px;
  margin-left: -35px;
  text-align: center;
  animation: preloader-rotate 2s infinite linear;
}
.preloader__item {
  position: absolute;
  display: inline-block;
  top: 0;
  background-color: skyblue;
  border-radius: 100%;
  width: 35px;
  height: 35px;
  animation: preloader-bounce 2s infinite ease-in-out;
}
.preloader__item:last-child {
  top: auto;
  bottom: 0;
  animation-delay: -1s;
}
@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes preloader-bounce {
  0%,
  100% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}
.about-me {
  width: 80%;
  max-width: 1440px;
  text-align: center;
  padding: 50px 0;
  margin: 0 auto;
}
.about-me__title {
  margin: 0 0 20px;
}
.about-me__text {
  margin: 0 0 20px;
}
.about-me__list {
  text-align: start;
  display: flex;
  flex-flow: row wrap;
  gap: 20px 0;
  margin: 0;
}
.contacts {
  display: flex;
  flex-flow: column;
}
.contacts__link {
  text-decoration: none;
  font-weight: 700;
  color: #B6452C;
  margin: 0 0 10px;
}
.contacts__link:last-child {
  margin: 0;
}
.courses {
  width: 100%;
  background: #eee9e9;
  padding: 50px 0;
}
.courses__wrapper {
  width: 80%;
  max-width: 1440px;
  color: black;
  margin: 0 auto;
}
.courses__title {
  text-align: center;
  margin: 0 0 30px;
}
.courses__list {
  width: 100%;
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 100px 0;
}
@media (min-width: 1024px) {
  .courses__list {
    width: 100%;
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    gap: 150px 0;
    margin: 0 auto;
  }
}
.courses__item {
  width: 100%;
  text-align: center;
  position: relative;
}
@media (min-width: 1024px) {
  .courses__item {
    width: 30%;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
  }
}
.courses__item:first-child::before {
  display: none;
}
@media (min-width: 1024px) {
  .courses__item:first-child::before {
    content: "";
    width: 1px;
    height: 130px;
    display: block;
    position: absolute;
    bottom: -140px;
    left: 100%;
    transform: rotate(320deg);
    background: #ff0000;
  }
}
.courses__item:nth-child(2)::before {
  display: none;
}
@media (min-width: 1024px) {
  .courses__item:nth-child(2)::before {
    content: "";
    width: 1px;
    height: 110px;
    display: block;
    position: absolute;
    bottom: -130px;
    left: 50%;
    transform: translateX(-50%);
    background: #00ff22;
  }
}
.courses__item:nth-child(3)::before {
  display: none;
}
@media (min-width: 1024px) {
  .courses__item:nth-child(3)::before {
    content: "";
    width: 1px;
    height: 130px;
    display: block;
    position: absolute;
    bottom: -140px;
    left: 0%;
    transform: rotate(45deg);
    background: deepskyblue;
  }
}
@media (min-width: 1024px) {
  .courses__item:last-child {
    margin: 0 auto;
  }
}
.courses__text {
  margin: 0;
}
@media (min-width: 1024px) {
  .courses__text {
    margin: 0 0 30px;
  }
}
.courses__picture {
  width: 50%;
  height: 400px;
  margin: 0 auto;
}
@media (min-width: 1024px) {
  .courses__picture {
    width: 80%;
    height: 400px;
    padding: 0;
    margin: 0 auto  ;
  }
}
.courses__image {
  width: 50%;
  height: 400px;
  display: block;
  margin: 0 auto;
  object-fit: contain;
}
@media (min-width: 1024px) {
  .courses__image {
    width: 100%;
    height: 400px;
    object-fit: contain;
  }
}
.education {
  width: 80%;
  max-width: 1440px;
  display: flex;
  align-items: center;
  padding: 50px 0;
  margin: 0 auto;
}
.education__info-container {
  width: 100%;
}
.education__title {
  margin: 0 0 30px;
}
.education__text {
  margin: 0 0 10px;
}
.education__svg {
  width: 20%;
  height: 100px;
}
.education:hover .education__svg-a {
  fill: orange;
}
.education:hover .education__svg-b {
  fill: deepskyblue;
}
.footer {
  width: 100%;
  color: white;
  background: black;
  padding: 0 0 25px;
}
.footer__wrapper {
  width: 80%;
  max-width: 1440px;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
}
.header {
  width: 100%;
  background: black;
  padding: 50px 0;
}
.header__wrapper {
  width: 80%;
  max-width: 1440px;
  margin: 0 auto;
}
.header__list {
  width: 100%;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: space-around;
}
.header__item:hover {
  background-color: deepskyblue;
  border-radius: 10px;
}
.header__item:hover .header__link {
  color: black;
}
.header__link {
  text-decoration: none;
  display: block;
  color: #FFFFFF;
  padding: 10px;
}
.hobby {
  width: 80%;
  max-width: 1440px;
  padding: 50px 0;
  margin: 0 auto;
}
.hobby__title {
  text-align: center;
  margin: 0 0 50px;
}
.hobby__text {
  font-size: 16px;
  line-height: 20px;
  margin: 0 0 10px;
}
.hobby__list {
  width: 100%;
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 40px 5%;
  padding: 0;
  margin: 0 auto;
}
.hobby__item {
  min-height: 130px;
  text-align: center;
  display: flex;
  flex-flow: column;
  justify-content: space-around;
  align-items: center;
  color: black;
  background: white;
  border: 4px solid deepskyblue;
  border-radius: 50px;
}
.hobby__item:hover {
  filter: invert(100%);
}
.hobby__item-description {
  width: 80%;
  margin: 0;
}
.hobby__item-svg {
  width: 50px;
  height: 50px;
}
.hobby__button {
  font-family: inherit;
  font-size: 16px;
  line-height: 20px;
  border-radius: 20px;
  padding: 10px 20px;
  margin: 0;
}
.photo-gallery {
  width: 80%;
  max-width: 1440px;
  text-align: center;
  color: black;
  background: white;
  padding: 50px 0;
  margin: 0 auto;
}
.photo-gallery__title {
  margin: 0 0 50px;
}
.photo-gallery__container {
  width: 800px;
  height: 800px;
  display: flex;
  flex-flow: row wrap;
  margin: 0 auto;
}
.photo-gallery__slider {
  height: 400px;
  list-style: none;
  position: relative;
  padding: 0;
  margin: 0 0 20px 0;
  overflow: hidden;
}
.photo-gallery__slide {
  width: 400px;
  height: 400px;
  position: absolute;
  transition: all 0.5s;
}
.photo-gallery__picture,
.photo-gallery__image {
  width: 100%;
  height: 100%;
}
.photo-gallery__button-container {
  width: 80%;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
}
.photo-gallery__button {
  width: 150px;
  height: 50px;
  font-size: 20px;
  line-height: 22px;
  border: 3px solid deepskyblue;
  border-radius: 20px;
  color: black;
  background-color: #ffffff;
}
.portfolio {
  width: 100%;
  text-align: center;
  color: white;
  background: black;
  padding: 50px 0;
}
.portfolio__wrapper {
  width: 80%;
  max-width: 1440px;
  margin: 0 auto;
}
.portfolio__title {
  color: orange;
  margin: 0 0 30px;
}
.portfolio__text {
  margin: 0 0 20px;
}
.portfolio__list {
  width: 100%;
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  gap: 50px 10%;
}
.portfolio__item {
  width: 40%;
  background: black;
  border: 2px solid deeppink;
  border-radius: 40px;
}
.portfolio__link {
  text-decoration: none;
  text-align: center;
  display: flex;
  flex-flow: column;
  color: inherit;
  padding: 20px;
}
.portfolio__item:hover {
  transform: scale(95%);
  transition: 1s ease;
  border: 2px solid deepskyblue;
}
.portfolio__link-title {
  margin: 0 0 20px;
}
.portfolio__image {
  width: 100%;
  height: 300px;
  margin: 0 auto;
}
.portfolio__description {
  margin-bottom: 10px;
}
.portfolio__item-description:last-child {
  margin-bottom: 0;
}
.preview {
  width: 100%;
  max-width: 1440px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 50px 0;
  margin: 0 auto;
}
.preview__title {
  width: 80%;
  font-size: 26px;
  line-height: 30px;
  text-align: center;
  color: orange;
  margin: 0;
}
.skills {
  width: 100%;
  text-align: center;
  color: #241818;
  background: black;
  padding: 50px 0;
}
.skills__wrapper {
  width: 80%;
  max-width: 1440px;
  margin: 0 auto;
}
.skills__title {
  color: orange;
  margin: 0 0 50px;
}
.skills__text {
  color: orange;
  margin: 0 0 30px;
}
.skills__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px 40px;
  color: white;
  counter-reset: counter-name;
}
.skills__item {
  width: 100%;
  font-size: 22px;
  line-height: 34px;
  letter-spacing: -0.5px;
  text-align: center;
  position: relative;
  padding-top: 50px;
}
.skills__item::before {
  counter-increment: counter-name;
  content: "0" counter(counter-name);
  width: 100%;
  height: 55px;
  font-size: 28px;
  line-height: 38px;
  font-weight: 700;
  letter-spacing: -1.27px;
  position: absolute;
  top: 1px;
  left: 50%;
  transform: translateX(-50%);
  color: #0ad9c6;
  padding-top: 2px;
  box-sizing: border-box;
}
@keyframes shake {
  0%,
  50%,
  100% {
    transform: rotate(0deg);
  }
  10%,
  30% {
    transform: rotate(-10deg);
  }
  20%,
  40% {
    transform: rotate(10deg);
  }
}
.social__container {
  width: 50%;
  display: flex;
  justify-content: space-around;
  margin: 0 auto;
}
.social__link {
  width: 40px;
  height: 40px;
}
.social__vk,
.social__instagram {
  width: 40px;
  height: 40px;
}
.social__vk path,
.social__instagram path {
  width: 40px;
  height: 40px;
  fill: white;
}
.social__vk:hover,
.social__instagram:hover {
  animation: shake 1.2s ease-in-out;
}
