.portfolio {
  width: 100%;

  text-align: center;

  color: white;
  background: black;

  padding: 50px 0;
}

.portfolio__wrapper {
  width: 80%;
  max-width: 1440px;

  margin: 0 auto;
}

.portfolio__title {
  color: orange;

  margin: 0 0 30px
}

.portfolio__text {
  margin: 0 0 20px;
}

.portfolio__list {
  width: 100%;

  .reset-list;

  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  gap: 50px 10%;
}

.portfolio__item {
  width: 40%;

  background: black;

  border: 2px solid deeppink;
  border-radius: 40px;
}

.portfolio__link {
  text-decoration: none;
  text-align: center;

  display: flex;
  flex-flow: column;

  color: inherit;

  padding: 20px;
}

.portfolio__item:hover {
  transform: scale(95%);
  transition: 1s ease;
  border: 2px solid deepskyblue;
}

.portfolio__link-title {
  margin: 0 0 20px;
}

.portfolio__image {
  width: 100%;
  height: 300px;

  margin: 0 auto;
}

.portfolio__description {
  margin-bottom: 10px;
}

.portfolio__item-description:last-child {
  margin-bottom: 0;
}
