.courses {
  width: 100%;

  background: rgb(238, 233, 233);

  padding: 50px 0;
}

.courses__wrapper {
  width: 80%;
  max-width: 1440px;

  color: black;

  margin: 0 auto;
}

.courses__title {
  text-align: center;

  margin: 0 0 30px;
}

.courses__list {
  width: 100%;

  .reset-list;

  display: grid;
  grid-template-columns: 1fr;
  gap: 100px 0;

  @media (min-width: 1024px) {
    width: 100%;

    .reset-list;

    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    gap: 150px 0;

    margin: 0 auto;
  }
}

.courses__item {
  width: 100%;

  text-align: center;

  position: relative;

  @media (min-width: 1024px) {
    width: 30%;

    display: flex;
    flex-flow: column;
    justify-content: space-between;
  }
}

.courses__item:first-child::before {
  display: none;

  @media (min-width: 1024px) {
    content: "";

    width: 1px;
    height: 130px;

    display: block;

    position: absolute;
    bottom: -140px;
    left: 100%;
    transform: rotate(320deg);

    background: rgb(255, 0, 0);
  }
}

.courses__item:nth-child(2)::before {
  display: none;

  @media (min-width: 1024px) {
    content: "";

    width: 1px;
    height: 110px;

    display: block;

    position: absolute;
    bottom: -130px;
    left: 50%;
    transform: translateX(-50%);

    background: rgb(0, 255, 34);
  }
}

.courses__item:nth-child(3)::before {
  display: none;

  @media (min-width: 1024px) {
    content: "";

    width: 1px;
    height: 130px;

    display: block;

    position: absolute;
    bottom: -140px;
    left: 0%;
    transform: rotate(45deg);
    background: deepskyblue;
  }
}

.courses__item:last-child {
  @media (min-width: 1024px) {
    margin: 0 auto;
  }
}

.courses__text {
  margin: 0;

  @media (min-width: 1024px) {
    margin: 0 0 30px;
  }
}

.courses__picture {
  width: 50%;
  height: 400px;

  margin: 0 auto;

  @media (min-width: 1024px) {
    width: 80%;
    height: 400px;

    padding: 0;
    margin: 0 auto  ;
  }
}

.courses__image {
  width: 50%;
  height: 400px;

  display: block;

  margin: 0 auto;

  object-fit: contain;

  @media (min-width: 1024px) {
    width: 100%;
    height: 400px;

    object-fit: contain;
  }
}
