.education {
  width: 80%;
  max-width: 1440px;

  display: flex;
  align-items: center;


  padding: 50px 0;
  margin: 0 auto;
}

.education__info-container {
  width: 100%;
}

.education__title {
  margin: 0 0 30px;
}

.education__text {
  margin: 0 0 10px;
}

.education__svg {
  width: 20%;
  height: 100px;
}

.education:hover .education__svg-a {
  fill: orange;
}

.education:hover .education__svg-b {
  fill: deepskyblue;
}
