.header {
  width: 100%;

  background: black;

  padding: 50px 0;
}

.header__wrapper {
  width: 80%;
  max-width: 1440px;

  margin: 0 auto;
}

.header__list {
  width: 100%;

  font-size: 14px;
  line-height: 20px;
  text-align: center;

  .reset-list;

  display: flex;
  justify-content: space-around;
}

.header__item:hover {
  background-color: deepskyblue;
  border-radius: 10px;
}

.header__item:hover .header__link {
  color: black;
}

.header__link {
  text-decoration: none;

  display: block;

  color: @header-link;

  padding: 10px;
}
